.c{
    height: 100vh;
    position: relative;
}
.c-bg{
    height: 100%;
    width: 20px;
    background-color: green;
    position: absolute;
}
.c-wrapper{
    padding: 50px;
    display: flex;

}
.c-left{
    flex: 1;
}
.c-right{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.c-desc{
    font-weight: 200px;
}
form{
    margin-top: 20px;
}
input{
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;

}
textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;


}
button{
border: none;
background: green;
padding: 15px;
color: white;
cursor: pointer;
}

@media screen and (max-width:480px){
    .c-wrapper{
        flex-direction: column;
    }
    
}