.a{
    height: 100vh;
    display: flex;
    align-items: center;
    

}
.a-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    
    height: 100%;

    
}

.a-card.bg{
position: absolute;
top: 50px;
left: 50px;
background-color: #333;

}
.a-card{
    width: 60%;
    height: 70vh;
    border-radius: 50px;
    position: relative;
    overflow: hidden;
}
.a-image{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.a-right{
    flex: 1;
}
.a-award{
    width: 120px;
    height: 120px;
    border-radius: 30px;
    object-fit: cover;
}
.a-title{
    font-size: 30px;

}
.a-sub{
    padding: 20px 5px;
}
.a-award{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.a-award-texts{
    width: 70%;
}
@media screen and (max-width: 480px) {
    .a{
        flex-direction: column;
    
        text-align: center;
        margin-top: 5px;
    }
    .a-left{
        width: 100%;
    }
    .a-card{
        height: 30vh;
    }
    .a-card.bg, .a-award{
        display: none;
    }
    .award-texts{
        display: none;
    }
    .a-right{
        padding: 10px;
    }

    
}